const BLACKLISTED_KEY_CODES = [38];
const COMMANDS = {
  help:
    'Supported commands: <span class="code">about</span>, <span class="code">experience</span>, <span class="code">research</span>, <span class="code">education</span>, <span class="code">skills</span>,<span class="code"> portfolio</span>',
  about:
    "Hello, I'm Maharani from Indonesia. I'm enthusiastic about Information Security. Also I like painting pictures when I’m not working.",
  skills:
    '<span class="skill">Specialization:</span>Web Dev, Python Web Scrapping, SQL for Data Analytics, Cloud-based Operational IAMs<br><span class="skill">Training:</span>Security+, Alibaba Cloud Associate, AWS Cloud Associate, IBM Data Analyst Training, IBM Cybersecurity Training, Google IT Support Training, CCNA Security & CyberOps<br><span class="skill">Interest:</span>Penetration Testing, IR',
  education:
  '<span class="school">Graduate Study:</span>Information & Network Security Management<br><span class="school">Undergraduate Study:</span>Majoring Broadband Telecomunication, Electrical Engineering',
  //resume: "<a href='./resume.pdf' class='success link'>resume.pdf</a>",
  experience: '<span class="exp">Professional:</span>Tech Application Support, Tech Writer<br><span class="exp">Freelance:</span>QA Tester, Web Designer<br><span class="exp">Education:</span>Teaching Assistant for Information Security class',
  research: '<span class="school">Graduate Research:</span><br>The Blockchain Concept in building a Trusted Ecosystem in the Internet of Things<br><span class="school">Undergraduate Research:</span><br>Honeypot Webserver and Home Network Detection System using Raspberry Pi<br>Smart Aquaponic using Android',
  portfolio: '<span class="porto">Web Design:</span><a href="https://hannymaharani99.github.io/portfolio/ " target="_blank" rel="noreferrer noopener">Random Quote Generator</a><br><span class="porto">Red Team:</span><a href="https://medium.com/@maharanirach/jenkins-misconfig-exploitation-1e3afdac6130" target="_blank" rel="noreferrer noopener">TryHackMe Write-Up: Jenkins Misconfig Exploit</a><br><span class="porto">Static Web Application:</span><a href="https://hannymaharani99.github.io/musicplayer/" target="_blank" rel="noreferrer noopener">Mini Music Player App</a>'
};
let userInput, terminalOutput, year;


const app = () => {
  userInput = document.getElementById("userInput");
  year = document.getElementById("year").innerHTML = new Date().getFullYear();
  terminalOutput = document.getElementById("terminalOutput");
  document.getElementById("dummyKeyboard").focus();
  console.log("Application loaded");
};

const execute = function executeCommand(input) {
  let output;
  input = input.toLowerCase();
  if (input.length === 0) {
    return;
  }
  output = `<div class="terminal-line"><span class="success">➜</span> <span class="directory">~</span> ${input}</div>`;
  if (!COMMANDS.hasOwnProperty(input)) {
    output += `<div class="terminal-line">no such command: ${input}</div>`;
    console.log("Oops! no such command");
  } else {
    output += COMMANDS[input];
  }
  terminalOutput.innerHTML = `${
    terminalOutput.innerHTML
  }<div class="terminal-line">${output}</div>`;
  terminalOutput.scrollTop = terminalOutput.scrollHeight;

};


const key = function keyEvent(e) {
  const input = userInput.innerHTML;

  if (BLACKLISTED_KEY_CODES.includes(e.keyCode)) {
    return;
  }

  if (e.key === "Enter") {
    execute(input);
    userInput.innerHTML = "";
    this.clearInput()
    return;
  }

  userInput.innerHTML = input + e.key;
};

const backspace = function backSpaceKeyEvent(e) {
  if (e.keyCode !== 8 && e.keyCode !== 46) {
    return;
  }
  userInput.innerHTML = userInput.innerHTML.slice(
    0,
    userInput.innerHTML.length - 1
  );
};

document.addEventListener("keydown", backspace);
document.addEventListener("keypress", key);
document.addEventListener("DOMContentLoaded", app);
document.addEventListener('contextmenu', function(e) {
  e.preventDefault();
});

